.navbar {
    font-family: 'Libre Baskerville', serif;
    transition: background-color 500ms linear;
}

.navbar-brand {
    padding: 0 5px;
    border: 2px solid white;
}

.item-click {
    cursor: pointer;
}

.active {
    padding-bottom: 5%;
    border-bottom: 2px white solid;
}
@media only screen and (max-width: 991px) {
    .nav-item {
        text-align: right;
    }
    .active {
        padding-bottom: 1%;
    }
}

.home {
    font-family: 'Libre Baskerville', serif;
}
.display-1 {
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;
    font-size: 8rem;
}
.hero {
    width: 100vw;
    height: 100vh;

    color: white;

    /* flexbox */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    text-align: center;

    /* Background styles */
    background-image: linear-gradient(rgba(0, 0, 0, 0.3),rgba(0, 0, 0, 0.3)), url(/flower.jpeg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;


    /* transition: border-color 240ms ease-in-out;
    transition-property: border-color;
    transition-duration: 240ms;
    transition-timing-function: ease-in-out;
    transition-delay: 0s; */
}
.call-to-action {
    color: white;
    background-color: #21ce99;
    
    /* Border styles */
    /* border: 3px solid white; */
}
.btn-rounded {
    padding: 1em;
    border-radius: 50px;
}

.call-to-action:hover {
    color: white;
    background-color: #01ae79;
}

.message {
    padding: 200px 0;
    margin: 0 auto;
    text-align: center;
}
div {
    overflow: hidden;
}

.container-fluid {
    padding: 0;
}

.box {
    width: 80%;
    color: white;
    /* float: left; */
}

.box-right {
    width: 80%;
    color: white;
    /* float: right; */
}

.spacing {
    margin-bottom: 200px;
}
.box-padding {
    padding: 80px 50px;
}
.qs {
    background-color: #ff909c;
}
.hs {
    background-color: #3fa7d6;
}
.sc {
    background-color: #a4a1ec;
}

.heading-about {
    text-align: left;
}
